import React, { Component } from "react";
import axios from "axios";

class UserAddForm extends Component {
  state = {
    FormName: "",
    FormEmail: "",
    FormPassword: "",
    response: ""
  };

  handleSubmit = e => {
    e.preventDefault();

    let formData = new FormData();
    formData.append("name", this.state.FormName);
    formData.append("email", this.state.FormEmail);
    formData.append("password", this.state.FormPassword);
    formData.append("requestOF", "AddUser");

    axios({
      method: "post",
      url: "https://sarjsk991.000webhostapp.com/php_react/api/index.php",
      data: formData,
      config: { headers: { "Content-Type": "multipart/form-data" } }
    })
      .then(response => {
        this.setState({ response: response.data.response });
        // console.log(this.state.UsersData);
        alert(this.state.response);
      })
      .catch(function(response) {
        console.log("Error");
      });
  };
  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <div className="form-group">
          <label htmlFor="UserName">Name</label>
          <input
            type="text"
            className="form-control"
            id="UserName"
            placeholder="Enter Your Name"
            onChange={e => this.setState({ FormName: e.target.value })}
          />
        </div>
        <div className="form-group">
          <label htmlFor="UserEmail">Email</label>
          <input
            type="text"
            className="form-control"
            id="UserEmail"
            placeholder="youremail@gmail.com"
            onChange={e => this.setState({ FormEmail: e.target.value })}
          />
        </div>
        <div className="form-group">
          <label htmlFor="FirstName">Password</label>
          <input
            type="password"
            className="form-control"
            id="UserPassword"
            placeholder="Enter Your Password"
            onChange={e => this.setState({ FormPassword: e.target.value })}
          />
        </div>

        <button type="submit" className="btn btn-primary float-right">
          Submit
        </button>
      </form>
    );
  }
}

export default UserAddForm;
