import React, { Component } from "react";
import axios from "axios";
import { withRouter } from "react-router-dom";

class TRow extends Component {
  state = {
    data: this.props,
    response: ""
  };
  UserEdit = user => {
    console.log("Edit : " + user);
    window.location = "/edit/" + user;
  };

  UserDelete = user => {
    if (window.confirm("Are You Sure You Want to Delete This User?")) {
      let formData = new FormData();
      formData.append("user", user);
      formData.append("requestOF", "DeleteUser");
      axios({
        method: "post",
        url: "https://sarjsk991.000webhostapp.com/php_react/api/index.php",
        data: formData,
        config: { headers: { "Content-Type": "multipart/form-data" } }
      })
        .then(response => {
          this.setState({ response: response.data.response });
          // console.log(this.state.UsersData);
          alert(this.state.response);
        })
        .catch(function(response) {
          console.log("Error");
        });
    } else {
    }
  };
  render() {
    return (
      <tr>
        <th> {this.props.id} </th>
        <td align="center"> {this.props.firstN} </td>
        <td align="center"> {this.props.lastN} </td>
        <td align="center"> {this.props.handle} </td>
        <td align="center">
          <button
            className=" btn btn-warning "
            onClick={() => this.UserEdit(this.props.id)}
          >
            Edit <i className="fa fa-pencil-square-o" />
          </button>
        </td>
        <td align="center">
          <button
            className=" btn btn-danger"
            onClick={() => this.UserDelete(this.props.id)}
          >
            Delete <i className="fa fa-trash" />
          </button>
        </td>
      </tr>
    );
  }
}

export default withRouter(TRow);
