import React, { Component } from "react";
import axios from "axios";

class EditUser extends Component {
  state = {
    id: 0,
    name: "",
    email: "",
    uniqID: "",
    requestOF: "ShowUsers",
    userID: ""
  };

  UserEditSubmit = id => {
    let newName = this.state.name;
    let newEmail = this.state.email;
    alert(id + " " + newName + " " + newEmail);

    let formData = new FormData();
    formData.append("user", id);
    formData.append("newName", newName);
    formData.append("newEmail", newEmail);
    formData.append("requestOF", "UpdateUser");
    axios({
      method: "post",
      url: "https://sarjsk991.000webhostapp.com/php_react/api/index.php",
      data: formData,
      config: { headers: { "Content-Type": "multipart/form-data" } }
    })
      .then(response => {
        alert(response.data.response);
        this.UserEditCancle();
      })
      .catch(function(response) {
        alert("Error " + response);
      });
  };

  UserEditCancle = () => {
    this.props.history.push("/");
  };
  getUser = () => {
    let userID = window.location.pathname;
    userID = userID.substr(6);
    // this.setState({ userID: userID });
    let formData = new FormData();
    formData.append("requestOF", this.state.requestOF);
    formData.append("userID", userID);

    axios({
      method: "post",
      url: "https://sarjsk991.000webhostapp.com/php_react/api/index.php",
      data: formData,
      config: { headers: { "Content-Type": "multipart/form-data" } }
    })
      .then(response => {
        // console.log(response.data.data[0]);

        setInterval(() => {
          this.setState({ id: response.data.data[0].id });
          this.setState({ name: response.data.data[0].name });
          this.setState({ email: response.data.data[0].email });
          this.setState({ uniqID: response.data.data[0].uniqID });
        }, 5000);
        // console.log(this.state.UsersData);
      })
      .catch(function(response) {
        console.log("Error" + response);
      });
  };
  render() {
    this.getUser();

    return (
      <tr className="bg-info" onLoad={this.getUser()}>
        <th> {this.state.id} </th>
        <td align="center">
          <input
            className="form-control"
            value={this.state.name}
            onChange={e => this.setState({ name: e.target.value })}
          />
        </td>
        <td align="center">
          <input
            className="form-control"
            value={this.state.email}
            onChange={e => this.setState({ email: e.target.value })}
          />
        </td>
        <td align="center"> {this.state.uniqID} </td>
        <td align="center">
          <button
            className=" btn btn-warning "
            onClick={() => this.UserEditSubmit(this.state.id)}
          >
            Submit <i className="fa fa-pencil-square-o" />
          </button>
        </td>
        <td align="center">
          <button
            className=" btn btn-secondary"
            onClick={() => this.UserEditCancle()}
          >
            Cancle <i className="fa fa-trash" />
          </button>
        </td>
      </tr>
    );
  }
}

export default EditUser;
