import React, { Component } from "react";
import { Route, BrowserRouter as Router } from "react-router-dom";
import EditUser from "./EditUser";
import TRow from "./TRow";
import axios from "axios";

class TBody extends Component {
  state = {
    requestOF: "ShowUsers",
    UsersData: [],
    id: 0,
    name: "",
    email: "",
    uniqID: ""
  };

  apiFetch = () => {
    let formData = new FormData();
    formData.append("requestOF", this.state.requestOF);
    axios({
      method: "post",
      url: "https://sarjsk991.000webhostapp.com/php_react/api/index.php",
      data: formData,
      config: { headers: { "Content-Type": "multipart/form-data" } }
    })
      .then(response => {
        this.setState({ UsersData: response.data.data });
        // console.log(this.state.UsersData);
      })
      .catch(function(response) {
        console.log("Error");
      });
  };
  getUser = () => {
    let userID = window.location.pathname;
    userID = userID.substr(6);
    // this.setState({ userID: userID });
    let formData = new FormData();
    formData.append("requestOF", this.state.requestOF);
    formData.append("userID", userID);

    axios({
      method: "post",
      url: "https://sarjsk991.000webhostapp.com/php_react/api/index.php",
      data: formData,
      config: { headers: { "Content-Type": "multipart/form-data" } }
    })
      .then(response => {
        // console.log(response.data.data[0]);
        this.setState({ id: response.data.data[0].id });
        this.setState({ name: response.data.data[0].name });
        this.setState({ email: response.data.data[0].email });
        this.setState({ uniqID: response.data.data[0].uniqID });
        // console.log(this.state.UsersData);
      })
      .catch(function(response) {
        console.log("Error" + response);
      });
  };
  render() {
    this.apiFetch();
    // let urlloc = window.location.pathname;
    // let urllocedit = urlloc.substr(1, 4);
    // if (urllocedit === "edit") {
    //   this.getUser();
    // }

    return (
      <tbody>
        <Router>
          <Route path="/edit/:id" component={EditUser} />
        </Router>
        {this.state.UsersData.map(data => (
          <TRow
            key={data.id}
            id={data.id}
            firstN={data.name}
            lastN={data.email}
            handle={data.uniqID}
          />
        ))}
      </tbody>
      //
      //   <TRow id="1" firstN="Sarj" lastN="SK" handle="991" />
      //
    );
  }
}

export default TBody;
